function initQuickAddQuantitySelectors(node) {
  const delegate = new Delegate(node);

  delegate.on("click", selectors.quantitySubtract, (_, target) => {
    const { variantId, qty } = _getItemData(target);
    const newQuantity = qty - 1;

    cart.updateItem(variantId, newQuantity);
  });

  delegate.on("click", selectors.quantityAdd, (_, target) => {
    const { variantId } = _getItemData(target);

    cart.addItemById(variantId, 1);
  });

  delegate.on("change", selectors.quantityInput, (_, target) => {
    const { variantId } = _getItemData(target);

    cart.updateItem(variantId, parseInt(target.value, 10));
    add(target.closest(selectors.quantityWrapper), classes.updatingQuantity);
  });

  delegate.on("click", selectors.removeItem, (_, target) => {
    const { variantId, qty } = _getItemData(target);

    if (qty > 0) {
      cart.updateItem(variantId, 0);
      add(target.closest(selectors.quantityWrapper), classes.updatingQuantity);
    }
  });

  delegate.on(
    "click",
    [selectors.quantitySubtract, selectors.quantityAdd],
    (_, target) => {
      add(target.closest(selectors.quantityWrapper), classes.updatingQuantity);
    }
  );

  const events = [
    on("quick-add:error", (_, { id, errorMessage }) => {
      const item = node.closest(selectors.item); // TODO reconsider this, why does quick-add know about product-item?

      if (item.dataset.variantId === id) {
        console.log(errorMessage);
        _handleErrorMessage(item);
      }
    }),
  ];

  const unload = () => {
    events.forEach(unsubscribe => unsubscribe());
    delegate.off();
  };

  return {
    unload,
  };
}

function initQuickAddTemplateCounters(
  node,
  options = {
    afterLoad: false, // Set this to true if initialized beyond initial page load
  }
) {
  const events = [
    on("theme-internal:update-product-item-counts", ({ id, quantity }) => {
      const item = node.closest(selectors.item);

      if (parseInt(item.dataset.id, 10) === id) {
        _handleUpdateProductCount(item, quantity);
      }
    }),
    // Removing until quick add modal can be fleshed out.
    /* on(
      "theme-internal:update-product-item-modal-counts",
      ({ variantId, variantQuantity }) => {
        const modalTemplate = qs("template", node)?.content;
        modalTemplate &&
          updateModalVariantsCounts(variantId, variantQuantity, modalTemplate);
      }
    ), */
  ];

  if (options.afterLoad) {
    // If this UI is initialized after load (eg if the HTML was rendered in a template and lazy loaded)
    // we need to make sure the counts are correct in case the liquid differs from the current state
    let cartCount = 0;
    const cartItems = window.theme?.cartData?.items;
    const productId = node.dataset.productId;
    const thisProductInCartItems = cartItems.filter(
      item => item.product_id == productId
    );
    thisProductInCartItems.forEach(cartItem => {
      cartCount += cartItem.quantity;
    });

    const item = node.closest(selectors.item);

    _updateProductCartQuantity(item, cartCount);
    _handleUpdateProductCount(item, cartCount);
  }

  const unload = () => {
    events.forEach(unsubscribe => unsubscribe());
  };

  return {
    unload,
  };
}

