function _getItemData(target) {
  const item = target.closest(selectors.item);
  const { id, variantId } = item.dataset;
  const qty = parseInt(qs(selectors.quantityInput, item).value);

  return { item, id, variantId, qty };
}

function _updateProductCartQuantity(item, quantity) {
  const productId = item.dataset.id;

  const cartDisplayEls = qsa(
    selectors.cartQuantityCountDisplay(productId),
    item
  );

  // TODO: not sure we should be updating the template here...
  // Removing until quick add modal can be fleshed out.
  /* const modalTemplate = qs("template", item)?.content;

  if (
    modalTemplate &&
    qs(selectors.cartQuantityCountDisplay(productId), modalTemplate)
  ) {
    cartDisplayEls.push(
      qs(selectors.cartQuantityCountDisplay(productId), modalTemplate)
    );
  }*/

  cartDisplayEls.forEach(el => {
    el.textContent = quantity;
  });
}

function _handleUpdateProductCount(item, newQuantity) {
  item.dataset.quantityInCart = newQuantity;

  const elsWithDataQuantityInCart = qsa(
    selectors.elsWithDataQuantityInCart,
    item
  );

  elsWithDataQuantityInCart.forEach(el => {
    el.dataset.quantityInCart = newQuantity;
  });

  remove(qsa(selectors.quantityWrapper, item), classes.updatingQuantity);

  const productQuantityInputs = qsa(selectors.productQuantityInput, item);

  productQuantityInputs.forEach(input => {
    input.value = newQuantity;
  });

  if (newQuantity === 0) {
    const quickAddWrapper = qs(selectors.quickAddWrapper, item);
    remove(quickAddWrapper, classes.active);
  }

  _updateProductCartQuantity(item, newQuantity);
}

function _handleErrorMessage(item) {
  const itemEl = qs(selectors.quantityWrapper, item);
  const itemElInput = qs("input", itemEl);
  const cartQuantity = itemEl.closest(selectors.elsWithDataQuantityInCart)
    .dataset.quantityInCart;

  itemElInput.value = cartQuantity;
  remove(itemEl, classes.updatingQuantity);
}

